body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: 'bariolregular';
	src: url(/static/media/bariol_regular-webfont.6a0a772f.eot);
	src: url(/static/media/bariol_regular-webfont.6a0a772f.eot?#iefix) format('embedded-opentype'),
				url(/static/media/bariol_regular-webfont.3226f53c.woff2) format('woff2'),
				url(/static/media/bariol_regular-webfont.32397546.woff) format('woff'),
				url(/static/media/bariol_regular-webfont.d06eb55c.ttf) format('truetype'),
				url(/static/media/bariol_regular-webfont.268f6193.svg#bariolregular) format('svg');
	font-weight: normal;
	font-style: normal;
}

* {
	font-family: 'bariolregular';
  box-sizing: border-box;
	letter-spacing: 1px;
}

html,
body,
#root {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1 1;
  color: #696969;
}

body {
  background: #f6f6f6;
}

a {
  color: #03a5df;
}

article figure img {
  width: 100%;
  height: auto;
}
